<template>
  <div id="app">
    <div class="content">
      <el-row type="flex" align="middle" class="row">
        <el-col :span="4">
          <router-link to="/PC/">
            <div>
              <img src="/static/logo.png" alt="" />
            </div>
          </router-link>
        </el-col>
        <el-col :span="20">
          <!--  -->
          <el-row>
            <el-col :span="12" :push="1">
              <el-row>
                <el-col>
                  <div style="margin-top: 4px" @keyup.enter="search()">
                    <el-autocomplete
                      v-model="searchData"
                      :fetch-suggestions="querySearch"
                      placeholder=""
                      :trigger-on-focus="true"
                      @select="handleSelect"
                      style="display: block"
                    >
                      <el-button
                        slot="append"
                        icon="el-icon-search"
                        @click="search()"
                      ></el-button>
                    </el-autocomplete>
                  </div>
                </el-col>
              </el-row>
              <el-row
                :gutter="0"
                type="flex"
                justify="space-between"
                style="margin-top: 4px"
              >
                <el-col>
                  <el-link :underline="false"></el-link>
                </el-col>
                <el-col>
                  <el-link :underline="false"></el-link>
                </el-col>
                <el-col>
                  <el-link :underline="false"></el-link>
                </el-col>
                <el-col>
                  <el-link :underline="false"></el-link>
                </el-col>
                <el-col>
                  <el-link :underline="false"></el-link>
                </el-col>
                <el-col>
                  <el-link :underline="false"></el-link>
                </el-col>
                <el-col>
                  <el-link :underline="false"></el-link>
                </el-col>
                <el-col>
                  <el-link :underline="false"></el-link>
                </el-col>
              </el-row>
            </el-col>
            <!--  -->
            <el-col :span="4" :push="2" style="margin-top: 4px">
              <router-link to="/PC/cart">
                <el-badge :value="getCartsLength" :max="99" class="item">
                  <el-button icon="el-icon-shopping-cart-full" size="medium">
                    </el-button
                  >
                </el-badge>
              </router-link>
            </el-col>
            <!-- / -->
          </el-row>
          <!-- / -->
          <!--  -->
          <el-row class="searchList" :gutter="8">
            <el-col v-for="(item, index) in lists" :key="index" :span="2">
              <el-link :underline="false">
                {{ item }}
              </el-link>
            </el-col>
          </el-row>
          <!-- / -->
        </el-col>
      </el-row>
    </div>
  </div>
</template>
 
<script>
export default {
  name: 'Search',
  data() {
    return {
      searchData: '',
      restaurants: [],
      lists: [
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
      ],
    };
  },
  computed: {
    getCartsLength() {
      if (this.$store.state.userInfo === null) {
        return 0;
      } else {
        return this.$store.state.userInfo.carts.length || 0;
      }
    },
  },
  methods: {
    querySearch(queryString, cb) {
      var restaurants = this.restaurants;
      var results = queryString
        ? restaurants.filter(this.createFilter(queryString))
        : restaurants;
      //  callback 
      cb(results);
    },
    createFilter(queryString) {
      return (restaurant) => {
        // 
        let patt = new RegExp(queryString.toLowerCase());
        // console.log (patt);
        // console.log (queryString.toLowerCase());
        // 
        return patt.test(restaurant.value.toLowerCase());
      };
    },
    loadAll() {
      return [
        { value: '' },
        { value: '' },
        { value: '' },
        { value: 'realme' },
        { value: 'apple' },
        { value: ' | ' },
        { value: ' | ' },
        { value: ' | ' },
        { value: ' | ' },
      ];
    },
    handleSelect(item) {
      console.log(item);
      this;
    },
    search() {
      this.$router.push(`/PC/search?q=${this.searchData}&page=1&size=10`);
    },
  },
  mounted() {
    this.restaurants = this.loadAll();
  },
};
</script>

<style scoped>
#app {
  background-color: #fff;
  height: 200px;
  box-shadow: 0 4px 4px #ccc;
}

.content {
  width: 1200px;
  height: 100%;
  margin: 0 auto;
}

.row {
  height: 100%;
}

.searchList {
  position: relative;
  top: 38px;
}

.searchList .el-link {
  font-size: 16px;
}
</style>


