<template>
  <div>
    <header-part />
    <search-simple />
    <!--  -->
    <div class="toImg">
      <transition name="shop_cart">
        <img
          :src="imgs[0]"
          v-if="isShow"
          alt=""
          width="80px"
          height="80px"
          class="my_img"
        />
      </transition>
    </div>
    <!-- / -->
    <!--  -->
    <div class="detail">
      <div class="core">
        <div class="top" style="display: flex">
          <!--  -->
          <div class="imgs">
            <div class="mainImg">
              <pic-zoom
                :url="getMainImg || '/static/loading.png'"
                :scale="2"
              ></pic-zoom>
            </div>
            <div class="outerBox">
              <div class="rowBtn left" @click="moveLeft">
                <i class="el-icon-arrow-left"></i>
              </div>
              <div class="box">
                <div ref="innerBox" class="innerBox">
                  <div
                    class="imgItem inline-block"
                    v-for="(item, index) in imgs"
                    :key="index"
                    @mouseover="hoverImg(index)"
                  >
                    <img :src="item" alt="" width="60px" height="60px" />
                  </div>
                </div>
              </div>
              <div class="rowBtn left" @click="moveRight">
                <i class="el-icon-arrow-right"></i>
              </div>
            </div>
          </div>
          <!-- / -->
          <!--  -->
          <div class="right">
            <div class="title">
              {{ goodsDeatail[''] }}
            </div>
            <div class="subtitle" style="color: #e4393c; font-size: 12px">
              6++++19940001600
            </div>
            <div class="secKill">
              <i class="el-icon-alarm-clock" style="font-size: 18px"></i>
              <span></span>
            </div>
            <div class="valueCard">
              <div class="value">
                <span class="label"></span>
                <span class="rmb"></span>
                <span v-text="getPrice"> </span>
              </div>
              <div class="cuxiao">
                <span class="label"></span>
                <span class="free">
                  <span>
                    <el-tag type="danger" size="small" effect="plain">
                      
                    </el-tag>
                  </span>
                  <span v-for="(item, index) in 4" :key="index">
                    <img
                      src="/static/images/promotion/4.jpg"
                      alt=""
                      title=" 6+128G 6.3   (5G+64G)"
                      width="30px"
                      height="30px"
                      style="vertical-align: text-bottom"
                    />
                    <span class="multi">1</span>
                  </span>
                </span>
                <div class="limitBuy">
                  <el-tag type="danger" size="small" effect="plain">
                    
                  </el-tag>
                  <span class="des">
                    1-20
                  </span>
                </div>
              </div>
            </div>

            <el-divider></el-divider>

            <div class="buyType">
              <div>
                <span class="label">  </span>
                <span
                  class="inline-block"
                  v-for="(item, index) in tagList"
                  :key="index"
                  style="margin:0 8px 0 0"
                >
                  <el-tag
                    :type="getTagType(index)"
                    size="small"
                    effect="plain"
                    @click="switchTag(index)"
                    >{{ item }}</el-tag
                  >
                </span>
              </div>
              <!--  -->
              <div>
                <span class="label"> : </span>
                <el-button size="mini">
                  <i class="el-icon-mobile-phone"></i>
                  </el-button
                >
                <el-button size="mini">
                  <i class="el-icon-mobile-phone"></i>
                  
                </el-button>
                <el-button size="mini">
                  <i class="el-icon-mobile-phone"></i>
                  </el-button
                >
              </div>
              <!-- / -->
              <!--  -->
              <div>
                <span class="label"> : </span>
                <el-button size="mini">
                  <i class="el-icon-money"></i>
                  {{ goodsDeatail[''] }}  
                </el-button>
                <el-button size="mini">
                  <i class="el-icon-tickets"></i>
                  {{ getDivied(3) }}  3
                </el-button>
                <el-button size="mini">
                  <i class="el-icon-tickets"></i>
                  {{ getDivied(6) }}6
                </el-button>
                <el-button size="mini">
                  <i class="el-icon-tickets"></i>
                  {{ getDivied(12) }}12
                </el-button>
              </div>
              <!-- / -->
            </div>

            <el-divider></el-divider>

            <div class="buyArea">
              <div class="label"></div>
              <div>
                <el-input-number
                  size="small"
                  v-model="num"
                  controls-position="right"
                  :min="1"
                  :max="10"
                ></el-input-number>
              </div>
              <div>
                <el-button type="danger" @click="addToCart"
                  ></el-button
                >
              </div>
            </div>

            <div class="text" style="font-size: 14px; color: #aaa">
                7
            </div>
          </div>
          <!-- / -->
        </div>
        <!--  -->
        <div class="bottom">
          <div class="shopCard">
            <el-card shadow="hover" body-style="padding:2px">
              <div class="shopTitle">
                {{ goodsDeatail[''] || '' }}
              </div>
              <div style="margin-left: 8px; margin-top: 6px">
                <div class="star" style="display: flex">
                  <span></span>
                  <el-rate
                    :value="4.5"
                    disabled
                    text-color="#ff9900"
                    score-template="{value}"
                  >
                  </el-rate>
                </div>
                <div class="goodsComment">
                  <span></span>
                  <span class="comment"> 9.65  </span>
                </div>
                <div class="goodsComment">
                  <span></span>
                  <span class="comment">9.28 </span>
                </div>
                <div class="goodsComment">
                  <span></span>
                  <span class="comment">9.39 </span>
                </div>
              </div>
              <el-divider></el-divider>

              <div class="allCenter" style="padding-bottom: 20px">
                <el-button type="info" plain size="small">
                  <i class="el-icon-shopping-bag-2"></i>
                  
                </el-button>
                <el-button type="info" plain size="small">
                  <i class="el-icon-star-on"></i>
                  
                </el-button>
              </div>
            </el-card>
          </div>
          <div class="bottomRight">
            <el-tabs type="border-card">
              <el-tab-pane label="">
                <div v-html="goodsDeatail['HTML']"></div>
              </el-tab-pane>
              <el-tab-pane label="">
                <div style="color: #aaa" class="sizeBox">
                  <div
                    style="width: 350px"
                    v-for="(item, index) in getCanshu"
                    :key="index"
                  >
                    {{ item }}
                  </div>
                </div>
              </el-tab-pane>
              <el-tab-pane label="">
                <comments />
              </el-tab-pane>
            </el-tabs>
          </div>
        </div>
        <!-- / -->
      </div>
    </div>
    <!-- / -->
  </div>
</template>
 
<script>
import HeaderPart from 'PC/header/HeaderPart.vue';
import SearchSimple from 'PC/search/SearchSimple.vue';
import PicZoom from 'vue-piczoom';
import Comments from 'PC/components/Comments.vue';

export default {
  name: 'Detail',
  components: { HeaderPart, SearchSimple, PicZoom, Comments },
  data() {
    return {
      goodsDeatail: '',
      sku: this.$route.params.id,
      index: 0,
      imgs: [],
      num: 1,
      isShow: false,
      left: 0,
      imgWidth: 60,
      meanwhileNum: 6,
      tagIndex: 0,
      tagList: ['', '', '', '', ''],
    };
  },
  computed: {
    getPrice(item) {
      return this.goodsDeatail[''] - 0 + this.tagIndex * 100 || 0;
    },
    getMainImg() {
      return this.imgs[this.index];
    },
    getCanshu() {
      // [0] splice(1),
      return (this.goodsDeatail[''] || '').split('\n').splice(1);
    },
    imgNum() {
      return this.imgs.length || 0;
    },
  },
  methods: {
    getTagType(index){
      if (this.tagIndex === index){
        return 'primary'
      }
      return 'info';
    },
    moveLeft() {
      // console.log(`translateX(${this.left + 20}px)`);
      if (this.left < 0) {
        this.$refs.innerBox.style['transform'] = `translateX(${
          this.left + 60
        }px)`;
        this.left += 60;
      }
    },
    moveRight() {
      // console.log(`translateX(${this.left + 20}px)`);
      let minLeft = 0 - (this.imgNum - this.meanwhileNum) * this.imgWidth;
      if (minLeft < this.left) {
        this.$refs.innerBox.style['transform'] = `translateX(${
          this.left - 60
        }px)`;
        this.left -= 60;
      }
    },
    hoverImg(index) {
      this.index = index;
    },
    getDivied(value) {
      return (this.goodsDeatail[''] / value || 5000).toFixed(2);
    },
    addToCart() {
      let item = this.goodsDeatail;
      item['num'] = this.num;

      let users = JSON.parse(localStorage.getItem('users'));
      let userInfo = this.$store.state.userInfo;
      if (userInfo === null) {
        this.$message({
          showClose: true,
          message: '!',
          type: 'info',
        });
        this.$router.push('/PC/login?isLogin=true');
      } else {
        // 
        this.isShow = true;
        // ,,,
        setTimeout(() => {
          this.isShow = false;
        }, 1000);

        // 
        let currentUser = users.filter((item) => {
          return item['username'] === userInfo['username'];
        })[0];
        console.log(currentUser);
        currentUser.carts.push(item);
        this.$store.state.userInfo = currentUser;

        let i = users.find((item) => item.username == userInfo['username']);
        console.log('' + users.indexOf(i));

        users.splice(users.indexOf(i), 1, currentUser);
        // state
        localStorage.setItem('users', JSON.stringify(users));
        this.$store.commit('updateUsers', users);

        // this.$message({
        //   type: 'success',
        //   message: '',
        // });
      }
    },
    switchTag(index) {
      this.tagIndex = index;
    },
  },
  // goodDetail
  beforeCreate() {
    this.$axios({
      type: 'get',
      url: '/static/data/goodsDetails.json',
    })
      .then((res) => {
        console.log('');
        let data = res.data;
        // console.log(data);
        const d = data.filter((item) => {
          return item.sku == this.$route.params.id;
        });
        // console.log(d[0]);
        this.goodsDeatail = d[0];

        // 
        let str = this.goodsDeatail[''];
        // console.log(this.goodsDeatail['']);
        // 
        let arr = str.split('/\r\n');
        arr[arr.length - 1] = arr[arr.length - 1].substring(
          0,
          arr[arr.length - 1].length - 1
        );
        // console.log(arr);
        this.imgs = arr;
      })
      .catch((err) => {
        console.log(err);
      });
  },
};
</script>

<style scoped>
.toImg {
  position: absolute;
  top: 25px;
  left: 965px;
  z-index: 999;
}

.shop_cart-enter-active {
  transition: all 1s ease-out;
}

.shop_cart-enter {
  opacity: 0;
  transform: scale(0.5);
  transform: translate(-965px, 780px);
}

.innerBox{
  /*   */
  transition:all 0.4s linear;
}

.rowBtn {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 88px;
  background: #fff;
  cursor: pointer;
}

.rowBtn:hover i {
  color: #aaa !important;
}

.rowBtn i {
  color: #ddd;
  font-size: 40px;
}

.sizeBox {
  width: 800px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-start;
}

.pic-box {
  width: 500px;
  height: 500px;
}

.detail {
  width: 1200px;
  margin: 20px auto;
}
.detail .imgs {
  background: #fff;
  padding: 2px;
}

.detail .imgs .outerBox {
  margin-left: 8px;
  display: flex;
}

.detail .imgs .box {
  width: 360px;
  margin: 12px 0;
  overflow: hidden;
}

.detail .imgs .innerBox {
  display: flex;
  flex-wrap: nowrap;
}

.detail .imgs .mainImg {
  width: 450px;
  height: 450px;
  border: 1px solid #eee;
  background: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
}

.detail .core .right {
  background: #fff;
  padding: 4px 10px;
}

.detail .core .right .title {
  color: #444;
}

.detail .core .right .secKill {
  padding: 2px 4px;
  background: url('/static/images/seckilling.jpg');
  color: #fff;
}

.detail .core .right .valueCard {
  padding: 10px 4px;
  background: #f3f3f3;
}

.detail .core .right .label {
  font-size: 14px;
  color: #333;
  letter-spacing: 4px;
}

.detail .core .right .valueCard .rmb {
  font-size: 12px;
  color: #333;
  color: #e4393c;
}

.detail .core .right .valueCard .value {
  font-size: 20px;
  color: #e4393c;
}

.detail .core .right .free .multi {
  color: #e4393c;
}

.detail .core .right .free img {
  margin: 2px 4px;
}

.limitBuy {
  margin-left: 76px;
  font-size: 14px;
  color: #333;
  margin-top: 4px;
}

.buyType div:nth-child(2) {
  margin: 6px 0;
}

.buyArea {
  display: flex;
  justify-content: left;
  align-items: center;
}

.buyArea div:nth-child(2) {
  margin: 20px;
}

.shopCard {
  width: 210px;
  height: 230px;
  margin-right: 60px;
  border-radius: 8px;
  /* border: 1px solid #eee; */
  background: #fff;
  color: #999;
}

.shopCard .shopTitle {
  color: #666666;
  font-size: 18px;
  font-weight: 500;
  background: #f7f7f7;
  text-align: center;
  padding: 8px 0;
}

.comment {
  color: #8fc1b1;
}

.bottom {
  display: flex;
  margin-top: 20px;
}

.el-card__body {
  padding: 2px !important;
}
</style>


