<template>
  <div class="cart">
    <div class="core">
      <h2></h2>
      <el-divider></el-divider>
      <!--  -->
      <el-table
        :header-cell-style="{ 'text-align': 'center' }"
        ref="multipleTable"
        :data="tableData"
        tooltip-effect="dark"
        style="width: 100%"
        @selection-change="handleSelectionChange"
      >
        <el-table-column type="selection" width="55"> </el-table-column>
        <el-table-column label="" width="300" align="left">
          <template slot-scope="scope">
            <div class="goods">
              <img
                :src="getImg(scope.row[''])"
                alt=""
                class="pointer"
                width="120px"
                height="120px"
                @click="toPage('/PC/detail/'+scope.row['sku'])"
              />
              <span v-text="getTitle(scope.row[''])"> </span>
            </div>
          </template>
        </el-table-column>
        <!--    num   -->
        <el-table-column align="center" prop="" label="" width="120">
        </el-table-column>
        <el-table-column
          align="center"
          prop="num"
          label=""
          show-overflow-tooltip
        >
          <template slot-scope="scope">
            <el-input-number
              size="small"
              v-model="scope.row['num']"
            ></el-input-number>
          </template>
        </el-table-column>
        <el-table-column
          align="center"
          label=""
          show-overflow-tooltip
        >
          <template slot-scope="scope">
            <span>
              <span style="font-size:12px;">  </span> 
              {{getValue(scope.$index)}}
            </span>
          </template>
        </el-table-column>
        <el-table-column align="center" label="" show-overflow-tooltip>
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="danger"
              @click="handleDelete(scope.$index, scope.row)"
              ></el-button
            >
          </template>
        </el-table-column>
      </el-table>
      <div style="margin-top: 20px" class="bottom">
        <div>
          <el-button @click="selectAll(tableData)"></el-button>
          <el-button @click="toggleSelection()"></el-button>
          <!-- <el-button @click="logSelect()"></el-button> -->
        </div>

        <div class="value">
          <div class="total inline-block">
            <span></span>
            <span>  </span>
            <span class="money">{{ totalValue }}</span>
          </div>
          <el-button @click="toPay()" type="primary" plain>
            
          </el-button>
        </div>
      </div>
    </div>
  </div>
</template>
 
<script>
export default {
  name: 'MyCart',
  computed: {
    totalValue() {
      // 
      if (!this.isMounted) {
        return;
      }
      let arr = this.$refs.multipleTable.selection;
      console.log(arr);
      let total = 0;
      arr.forEach((item) => {
        total += item[''] * item['num'];
      });
      return total;
    },
  },
  data() {
    return {
      tableData: this.$store.state.userInfo.carts,
      multipleSelection: [],
      isMounted: false,
    };
  },
  methods: {
    toPage(item){
      this.$router.push(item);
    },
    getValue(index) {
      return (this.tableData[index][''] * this.tableData[index]['num']).toFixed(2);
    },
    toPay() {
      let arr = this.$refs.multipleTable.selection;
      if (arr.length <= 0) {
        // 
        this.$message({
          type: 'warning',
          message: '!',
        });
      } else {
        console.log(arr);
        this.$router.push({
          name: 'pay',
          path: '/PC/pay',
          params: {
            data: arr,
          },
          query: {
            userId: 1,
          },
        });
      }
    },
    addNum(item) {
      console.log(item);
    },
    getTitle(item) {
      return item;
    },
    getImg(item) {
      // console.log(item.split("/\r\n"));
      return item.split('/\r\n')[0];
    },
    handleDelete(index, row) {
      console.log(index);
      console.log(row);
      this.tableData.splice(index, 1);

      let users = localStorage.getItem('users');
      users = JSON.parse(users);
      let userInfo = this.$store.state.userInfo;
      // localstorage
      // 
      let currentUser = users.filter((item) => {
        return item['username'] === userInfo['username'];
      })[0];
      console.log(currentUser);
      currentUser.carts = this.tableData;
      this.$store.state.userInfo = currentUser;

      let i = users.find((item) => item.username == userInfo['username']);
      console.log('' + users.indexOf(i));

      users.splice(users.indexOf(i), 1, currentUser);
      // state
      localStorage.setItem('users', JSON.stringify(users));
      this.$store.commit('updateUsers', users);
    },
    toggleSelection(rows) {
      if (rows) {
        rows.forEach((row) => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    selectAll(rows) {
      console.log('');
      this.$refs.multipleTable.clearSelection();
      if (rows) {
        rows.forEach((row) => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      }
    },
    logSelect() {
      console.log(this.$refs.multipleTable);
      console.log(this.$refs.multipleTable.selection);
    },
  },
  mounted() {
    this.isMounted = true;
    this.$refs.multipleTable.toggleAllSelection();
  },
};
</script>

<style scoped>
.cart {
  width: 1000px;
  margin: 40px auto;
  padding: 10px 20px;
  background: #fff;
}

.goods {
  display: flex;
  align-items: center;
}

.cart .bottom {
  display: flex;
  justify-content: space-between;
}

.cart .bottom .value {
  display: flex;
  align-items: center;
}
.cart .bottom .value {
  color: #666;
  font-size: 14px;
  padding: 0 10px;
}

.cart .bottom .value .total {
  margin: 0 8px;
}

.cart .bottom .value .money {
  font-size: 18px;
}
</style>


