import axios from 'axios'
import Cookies from 'js-cookie'
export default {
  increaseCount(state) {
    state.count += 5;
  },
  updateUsers(state, users) {
    state.users = users;
  },
  login(state, userInfo) {
    this.state.userInfo = userInfo;
  },
  loadDefaultUsers(state) {
    let users = localStorage.getItem("users");
    console.log("loadDefaultUsers");
    if (!users) {
      // 
      axios({
          type: 'get',
          url: '/static/data/default-users.json'
        })
        .then((res) => {
          console.log(res.data);
          localStorage.setItem("users", JSON.stringify(res.data));
          state.users = JSON.parse(res.data);
        })
        .catch((err) => {
          console.log(err);
        });
    } else {
      state.users = JSON.parse(users);
    }
  },
  getUsersAndCheck(state, info) {
    //  state.users
    let users = localStorage.getItem("users");
    console.log("getUsersAndCheck");
    console.log("");
    console.log(info.userInfo);
    if (!users) {
      // 
      axios({
          type: 'get',
          url: '/static/data/default-users.json'
        })
        .then((res) => {
          console.log(res.data);
          localStorage.setItem("users", JSON.stringify(res.data));
          state.users = JSON.parse(res.data);
        })
        .catch((err) => {
          console.log(err);
        });
    } else {
      state.users = JSON.parse(users);
    }

    // checkinfo.userInfo
    users = state.users;
    console.log("");
    console.log(users);

    let newArr = users.filter((item) => {
      return item.username === info.userInfo.username;
    })
    console.log(newArr);

    if (newArr.length === 0) {
      info.that.$message({
        showClose: true,
        message: '',
        type: 'error'
      });
      return false;
    } else {
      if (newArr[0].password !== info.userInfo.password) {
        info.that.$message({
          showClose: true,
          message: '',
          type: 'error'
        });
        return false;
      } else {
        info.that.$message({
          showClose: true,
          message: ', ',
          type: 'success'
        });
        state.userInfo = newArr[0];

        console.log('----------------');
        console.log("");

        //7cookie
        Cookies.set('isLogin', 'true', {
          expires: 7
        });
        // info.that.$router.push('/PC/index');
        info.that.$router.go(-1);
        return true;
      }
    }
  },
  checkRegAndAdd(state, info) {
    //  state.users
    let users = localStorage.getItem("users");
    console.log("checkRegAndAdd");
    console.log("");
    console.log(info.userInfo);
    if (!users) {
      // 
      axios({
          type: 'get',
          url: '/static/data/default-users.json'
        })
        .then((res) => {
          console.log(res.data);
          localStorage.setItem("users", JSON.stringify(res.data));
          state.users = JSON.parse(res.data);
        })
        .catch((err) => {
          console.log(err);
        });
    } else {
      state.users = JSON.parse(users);
    }

    users = JSON.parse(users);
    console.log(users);


    let newArr = users.filter((item) => {
      return item.username === info.userInfo.username;
    })
    console.log(newArr);

    if (newArr.length !== 0) {
      info.that.$message({
        showClose: true,
        message: '',
        type: 'error'
      });
      return false;
    } else {
      info.that.$message({
        showClose: true,
        message: '',
        type: 'success'
      });

      let
        defaultUser = {
          "username": "root",
          "password": "123456",
          "name": "",
          "email": "s.zoq@vuyydkhus.sb",
          "age": 18,
          "headImg": "/static/images/headImg/5.jpg",
          "isPlus": 1,
          "carts": []
        };
      defaultUser.username = info.userInfo.username;
      defaultUser.name = info.userInfo.username;
      defaultUser.password = info.userInfo.password;

      users.push(defaultUser);
      console.log(users);
      localStorage.setItem("users", JSON.stringify(users));
      return true;
    }
  },
}



