<template>
  <div class="fullWidth recommend">
    <span class="text inline-block"></span>
    <div class="core fullWidth infinite-list" style="overflow: auto">
      <div
        class="card infinite-list-item pointer"
        v-for="(item, index) in getListData"
        :key="index"
        @click="toPage(item)"
      >
        <img :src="item['']" alt="" width="150px" height="150px" />
        <div class="des">
          <div>
            {{ item[''].substring(0, 40) + '...' }}
          </div>
        </div>
        <div
          class="footer"
          style="
            display: flex;
            color: #f00;
            font-size: 14px;
            align-items: flex-end;
          "
        >
          <div class="prefix"></div>
          <div class="value" style="font-size: 18px; font-weight: 600">
            {{ item[''] }}
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
 
<script>
export default {
  name: 'Recommend',
  data() {
    return {
      count: this.$store.getters.getStoreCount,
      lists: [],
    };
  },
  computed: {
    getListData() {
      return this.lists.slice(0, this.$store.getters.getStoreCount);
    },
  },
  methods: {
    load() {
      // this.count += 5;
      this.$store.commit('increaseCount');
    },
    toPage(item) {
      this.$router.push('/PC/detail/' + item.sku);
    },
    //  - 
    scrollTo() {
      // ()
      let scrollHeight =
        document.documentElement.scrollHeight || document.body.scrollHeight;
      // 
      let clientHeight =
        window.innerHeight ||
        document.documentElement.clientHeight ||
        document.body.clientHeight;
      // 
      let scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      // console.log (clientHeight);
      // console.log (scrollTop);
      // console.log (scrollHeight);
      if (clientHeight + scrollTop + 1 >= scrollHeight) {
        // 30
        console.log('');
        console.log(this.$store.getters.getStoreCount);
        console.log(this.$store.getters.getStoreCount >= 30);
        if (this.$store.getters.getStoreCount >= 30) {
          window.removeEventListener('scroll', scrollTo);
          console.log('-------------------');
          console.log('');
        } else {
          // 
          const loading = this.$loading({
            lock: true,
            text: 'Loading',
          });
          setTimeout(() => {
            this.load();
            loading.close();
          }, 1000);
        }
      }
    },
  },
  beforeMount() {
    // 
    this.$axios({
      type: 'get',
      url: '/static/data/goodsLists.json',
    })
      .then((res) => {
        this.lists = res.data.reverse();
      })
      .catch((error) => {
        console.log(error);
      });
  },
  //  
  created() {
    // 
    window.addEventListener('scroll', this.scrollTo);
  },
  // 
  destroyed() {
    // 
    console.log('-');
    console.log('');
    window.removeEventListener('scroll', this.scrollTo);
  },
};
</script>

<style scoped>
.recommend {
  text-align: center;
}

.recommend .text {
  padding: 20px 0;
  color: #000;
  font-size: 30px;
  font-weight: 600;
}

.core {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.core .card {
  width: 230px;
  height: 322px;
  margin: 6px 0;
  padding: 0 2px;
  background: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}

.core .card .des {
  color: #666;
  font-size: 14px;
}
</style>


