<template>
  <div>
    <header-part />
    <div class="pay">
      <div class="addresses">
        <el-card shadow="never">
          <div slot="header"></div>
          <div>
            <el-radio-group v-model="radio">
              <div
                class="selectItem"
                v-for="(item, index) in address"
                :key="index"
              >
                <el-radio :label="index">
                  <span>{{ item.province }}</span>
                  <span>{{ item.city }}</span>
                  <span>{{ item.subCity }}</span>
                  <span>{{ item.town }}</span>
                  <span>{{ item.DetailAddress }}</span>
                  <span> ({{ item.name }} ) </span>
                  <span>
                    {{ item.phone }}
                  </span>
                </el-radio>
              </div>
            </el-radio-group>
          </div>
        </el-card>
      </div>
      <div class="goods">
        <el-card shadow="never">
          <div slot="header"></div>
          <div>
            <div class="goodsItem" v-for="(item, index) in data" :key="index">
              <el-card shadow="hover">
                <div style="position: relative; top: 0; left: 0">
                  <div class="main">
                    <div class="name">
                      <div class="img">
                        <img
                          :src="getMainImg(item)"
                          alt=""
                          width="120px"
                          height="120px"
                        />
                      </div>
                      <div class="title">{{ item[''] }}</div>
                    </div>
                    <div class="value">
                      <div></div>
                      <div>
                        <span></span>
                        {{ item[''] }}
                      </div>
                    </div>
                    <div class="num">
                      <div></div>
                      <div>
                        {{ item['num'] }}
                      </div>
                    </div>
                    <div class="calc">
                      <div></div>
                      <div>
                        <span></span>
                        {{ getCalc(item) }}
                      </div>
                    </div>
                  </div>
                  <div class="transport">
                    <div>: </div>
                    <div></div>
                  </div>
                </div>
              </el-card>
            </div>
          </div>
          <el-divider />
          <div class="bottom">
             | 
            <div class="core">
              <!--  -->
              <el-card shadow="never">
                <div>
                  <span></span>
                  <span></span>
                  <span>
                    {{ getTotal() }}
                  </span>
                </div>
                <div>
                  <span></span>
                  <span>
                    {{ getAddress() }}
                  </span>
                </div>
                <div>
                  <span></span>
                  <span>{{ address[radio]['name'] }} </span>
                  <span>{{ address[radio]['phone'] }}</span>
                </div>
              </el-card>
              <!--  -->
              <div class="submitBtn">
                <el-button type="primary" @click="submitPay()">
                  
                </el-button>
              </div>
            </div>
          </div>
        </el-card>
      </div>
    </div>

    <my-footer />
  </div>
</template>
 
<script>
import MyFooter from '../footer/MyFooter.vue';
import HeaderPart from '../header/HeaderPart.vue';
export default {
  components: { MyFooter, HeaderPart },
  name: 'Pay',
  des: '',
  data() {
    return {
      time: 5,
      radio: 0,
      data: this.$route.params.data,
      address: [
        {
          province: '',
          city: '',
          subCity: '',
          town: '',
          DetailAddress: '45',
          name: '',
          phone: '18852937269',
        },
        {
          province: '',
          city: '',
          subCity: '',
          town: '',
          DetailAddress: '50',
          name: '',
          phone: '18852939969',
        },
        {
          province: '',
          city: '',
          subCity: '',
          town: '',
          DetailAddress: '221B',
          name: '',
          phone: '13952939969',
        },
      ],
    };
  },
  created() {
    console.log(this.$route);
  },
  methods: {
    getCalc(item) {
      return (item[''] * item['num']).toFixed(2);
    },
    getMainImg(item) {
      let str = item[''];
      let arr = str.split('/\r\n');
      return arr[0];
    },
    getTotal() {
      let total = 0;
      this.data.forEach((item) => {
        total += item[''] * item['num'];
      });
      return total.toFixed(2);
    },
    getAddress() {
      let obj = this.address[this.radio];
      let str =
        obj['province'] +
        ' ' +
        obj['city'] +
        ' ' +
        obj['subCity'] +
        ' ' +
        obj['town'] +
        ' ' +
        obj['DetailAddress'];
      return str;
    },
    submitPay() {
      // console.log('');
      // console.log(this.data);

      let str = `${this.getTotal()} , `;

      this.$confirm(str, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'primary',
        center: true,
      })
        .then(() => {
          let users = JSON.parse(localStorage.getItem('users'));
          let userInfo = this.$store.state.userInfo;
          // 
          let currentUser = users.filter((item) => {
            return item['username'] === userInfo['username'];
          })[0];
          console.log(currentUser);
          // 

          let flag;
          currentUser.carts = currentUser.carts.filter(item=>{
            flag = 0;
            this.data.forEach(element =>{
              if (element['sku'] === item['sku']){
                flag = 1;
              }
            })
            // flag0
            return flag === 0 ? true :false;
          })

          this.$store.state.userInfo = currentUser;

          let i = users.find((item) => item.username == userInfo['username']);
          console.log('' + users.indexOf(i));

          users.splice(users.indexOf(i), 1, currentUser);
          // state
          localStorage.setItem('users', JSON.stringify(users));
          this.$store.commit('updateUsers', users);
          // 
          this.data = [];
          this.$message({
            type: 'success',
            message: '',
          });
          this.$router.push('/PC/index');
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '',
          });
        });
    },
  },
};
</script>

<style scoped>
.pay {
  width: 1200px;
  margin: 0 auto;
  padding: 10px 20px;
}

.selectItem {
  margin: 10px 0;
}

.goods {
  margin-top: 30px;
}

.pay .goods .name {
  display: flex;
}

.pay .goods .title {
  width: 200px;
  height: 70%;
  text-align: left;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
}

.pay .goods .main {
  display: flex;
  justify-content: space-between;
  text-align: center;
}

.pay .goods .transport {
  position: absolute;
  right: 100px;
  bottom: 4px;
}

.pay .goods .transport {
  color: #666;
}

.pay .goods .transport > div {
  padding: 2px 0;
}

.goodsItem {
  margin-bottom: 20px;
}

.bottom {
  display: flex;
  justify-content: space-between;
}

.bottom .core {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-end;
}

.bottom .core .submitBtn {
  position: relative;
  top: 4px;
  right: 8px;
}
</style>


